<template>
  <div>
    <section class="hero is-primary">
      <div class="hero-body">
        <div class="container">
          <h1 class="title is-spaced" v-text="league.leagueCaption"></h1>
          <h4 class="subtitle">
            <router-link :to="{
                name: 'Team',
                params: { id: team.id }}"
                v-for="team in teams"
                :key="team.name"
              >
              <span class="image is-32x32">
                <img :alt="team.name" :src="team.crestUrl">
              </span>
            </router-link>
          </h4>
        </div>
      </div>
    </section>
    <div class="tabs">
      <ul>
        <router-link :to="{ name: 'LeagueTable', params: { 'competition': competition }}" tag="li">
          <a>Table</a>
        </router-link>
        <router-link :to="{ name: 'AllFixtures', params: { 'competition': competition }}" tag="li">
          <a>Fixtures</a>
        </router-link>
      </ul>
    </div>
    <div>
      <div class="container">
        <transition name="view">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
  name: 'League',
  props: {
    competition: {
      required: true,
      validator: (id) => id > 0,
    },
  },
  mounted() {
    if (!this.league.leagueCaption) {
      this.fetchLeague();
    }
    this.fetchTeams();
  },
  computed: {
    ...mapGetters({
      league: 'getLeagueTable',
      teams: 'getLeagueTeams',
    }),
  },
  methods: {
    fetchLeague() {
      this.$store.dispatch('fetchLeague', { id: this.competition, params: { matchday: this.matchDay } });
    },
    fetchTeams() {
      this.$store.dispatch('fetchTeams', { id: this.competition });
    },
  },
};
</script>
<style scoped>
  .subtitle .image { display: inline-block; margin-right: 10px; }
</style>



